<script>

//   };
// } else data = JSON.parse(localStorage.getItem("accshow_form"));
// console.log(data);
import VueMultiselect from "vue-multiselect";

export default {

  data() {
    var data;
// if (localStorage.getItem("accshow_form") == null) {
    data = {
      //json accshowd_evidence 3
      accshowd_evidence: [
        {
          accshow_evidence: "", //( )
          accshow_content: "", //( )
        },
      ],
      accshow_fact_reason: "", //()
      //json accshod_query_evidence 10
      accshowd_query_evidence: [
        {
          accshow_content: "", //
          accshow_fact_reason: "", //()
          accshow_defendant: "", //
          accshow_facticity: "true", //
          accshow_legality: "true", //
          accshow_relevance: "true", //
        },
      ],
    }
    var wholeItem = JSON.parse(localStorage.getItem(this.$store.state.court_number))
    if (wholeItem != null && "accshow_form" in wholeItem) {
      data = wholeItem.accshow_form
    }
    return {data: data}
  },
  mount() {

  },
  computed: {
    getProofPlaintiff: {
      get() {
        return this.data.accshowd_evidence.map(function (e) {
          return e.accshow_evidence;
        })
      },
    },
  },
  components: {
    VueMultiselect
  },
  methods: {
    add_component(datatype) {
      switch (datatype) {
        case "accshowd_evidence":
          //accshowd_evidence 
          this.data.accshowd_evidence.push({
            accshow_evidence: "", //( )
            accshow_content: "", //( )
            accshow_fact_reason: "", //()
          });
          break;
        case "accshowd_query_evidence":
          //
          this.data.accshowd_query_evidence.push({
            accshow_content: "", //
            accshow_fact_reason: "", //
            accshow_defendant: "", //
            accshow_query_evidence: "", // 
            accshow_query_reason: "", //
            accshow_facticity: "true", //
            accshow_legality: "true", //
            accshow_relevance: "true", //
          });
          break;

        default:
          //
          break;
      }
    },
    delete_component(datatype, index) {
      switch (datatype) {
        case "accshowd_evidence":
          //
          this.data.accshowd_evidence.splice(index, 1);
          break;
        case "accshowd_query_evidence":
          //
          this.data.accshowd_query_evidence.splice(index, 1);
          break;

        default:
          //
          break;
      }
    },
    // save_localstorage() {
    //   localStorage.setItem("accshow_form", JSON.stringify(this.data));
    // },
  },
  watch: {
    data: {
      handler() {
        //
        if (this.$store.state.court_number == "") {
          // window.layui.layer.msg('');
        } else {
          var wholeItem = JSON.parse(localStorage.getItem(this.$store.state.court_number))
          if (wholeItem != null) {
            wholeItem.accshow_form = this.data
            localStorage.setItem(this.$store.state.court_number, JSON.stringify(wholeItem))
          }
        }
      },
      deep: true
    }
  },
};
</script>


