<script>

import VueMultiselect from "vue-multiselect";

export default {
  data() {
     var data = {
        //json defendshow_proof 3
        defendshow_proof: [{
          defendshow_evidence: "",     //( )
          defendshow_content: "",       //( )
          defendshow_fact_reason: ""   //()
        }],
        //json defendshowd_accuser 10
        defendshowd_accuser: [{
          defendshow_accuser: "",        //
          defendshow_evidencenum: "",    //
          defendshow_facticity: "true",      //()
          defendshow_legality: "true", //()
          defendshow_relevance: "true",//()
          defendshow_query_reason: "", //()

        }],
        defendshowd_defendant: [{
          defendshow_defendant: "",        //()
          defendshow_numevidence: "",    //
          defendshow_numfacticity: "true",      //()
          defendshow_numlegality: "true", //())
          defendshow_numrelevance: "true",//())
          defendshow_query_reason: ''
        }],

        //
        defendshowd_evidence_counter: [{
          defendshow_evidence: "",     //( )
          defendshow_content: "",       //( )
          defendshow_fact_reason: ""   //()
        }],
        //json defendshowd_accuser 10
        defendshowd_accuser_counter: [{
          defendshow_accuser: "",        //
          defendshow_evidencenum: "",    //
          defendshow_facticity: "true",      //()
          defendshow_legality: "true", //()
          defendshow_relevance: "true",//()
          defendshow_query_reason: "", //()

        }],
        defendshowd_defendant_counter: [{
          defendshow_defendant: "",        //()
          defendshow_numevidence: "",    //
          defendshow_numfacticity: "true",      //()
          defendshow_numlegality: "true", //())
          defendshow_numrelevance: "true",//())
          defendshow_query_reason: ''
        }],
      };
    var wholeItem = JSON.parse(localStorage.getItem(this.$store.state.court_number))
    if (wholeItem != null && "defendshow_form" in wholeItem) {
      data = wholeItem.defendshow_form
    }
    return {
      data: data
    }
  },
  computed: {
    getProofDefendant: {
      get() {
        return this.data.defendshow_proof.map(function (e) {
          return e.defendshow_evidence;
        })
      },
    },
    getProofOfCounterDefendant: {
      get() {
        return this.data.defendshowd_evidence_counter.map(function (e) {
          return e.defendshow_evidence;
        })
      },
    },
  },

  components: {
    VueMultiselect
  },
  methods: {
    add_component(datatype) {
      switch (datatype) {
        case "defendshow_proof":
          //accshowd_evidence 
          this.data.defendshow_proof.push({
            defendshow_evidence: "",     //( )
            defendshow_content: "",       //( )
            defendshow_fact_reason: ""   //()
          });
          break
        case "defendshowd_accuser":
          //
          this.data.defendshowd_accuser.push({
            defendshow_accuser: "",        //
            defendshow_evidencenum: "",    //
            defendshow_facticity: "true",      //()
            defendshow_legality: "true", //()
            defendshow_relevance: "true",//()
            defendshow_query_facticity: "", //()
            defendshow_query_legality: "", //()
            defendshow_query_relevace: "", //()
          })
          break
        case "defendshowd_defendant":
          this.data.defendshowd_defendant.push({
            defendshow_defendant: "",        //()
            defendshow_numevidence: "",    //
            defendshow_numfacticity: "true",      //()
            defendshow_numlegality: "true", //())
            defendshow_numrelevance: "true",//())
            defendshow_reason_facticity: "", //()
            defendshow_reason_legality: "", //()
            defendshow_reason_relevace: "", //()
          })
          break
        case "defendshowd_evidence_counter":
          //accshowd_evidence 
          this.data.defendshowd_evidence_counter.push({
            defendshow_evidence: "",     //( )
            defendshow_content: "",       //( )
            defendshow_fact_reason: ""   //()
          });
          break
        case "defendshowd_accuser_counter":
          //
          this.data.defendshowd_accuser_counter.push({
            defendshow_accuser: "",        //
            defendshow_evidencenum: "",    //
            defendshow_facticity: "true",      //()
            defendshow_legality: "true", //()
            defendshow_relevance: "true",//()
            defendshow_query_facticity: "", //()
            defendshow_query_legality: "", //()
            defendshow_query_relevace: "", //()
          })
          break
        case "defendshowd_defendant_counter":
          this.data.defendshowd_defendant_counter.push({
            defendshow_defendant: "",        //()
            defendshow_numevidence: "",    //
            defendshow_numfacticity: "true",      //()
            defendshow_numlegality: "true", //())
            defendshow_numrelevance: "true",//())
            defendshow_reason_facticity: "", //()
            defendshow_reason_legality: "", //()
            defendshow_reason_relevace: "", //()
          })
          break
        default:
          //
          break
      }

    },
    delete_component(datatype, index) {
      switch (datatype) {
        case "defendshow_proof":
          //
          this.data.defendshow_proof.splice(index, 1)
          break
        case "defendshowd_accuser":
          //
          this.data.defendshowd_accuser.splice(index, 1)
          break
        case"defendshowd_defendant":
          this.data.defendshowd_defendant.splice(index, 1)
          break
        case "defendshowd_evidence_counter":
          //
          this.data.defendshowd_evidence_counter.splice(index, 1)
          break
        case "defendshowd_accuser_counter":
          //
          this.data.defendshowd_accuser_counter.splice(index, 1)
          break
        case"defendshowd_defendant_counter":
          this.data.defendshowd_defendant_counter.splice(index, 1)
          break
        default:
          //
          break
      }
    },
    // save_localstorage(){
    //   localStorage.setItem('defendshow_form',JSON.stringify(this.data))
    // }
  },
  watch: {
    data: {
      handler() {
        //
        if (this.$store.state.court_number == "") {
          // window.layui.layer.msg('');
        } else {
          var wholeItem = JSON.parse(localStorage.getItem(this.$store.state.court_number))
          if (wholeItem != null) {
            wholeItem.defendshow_form = this.data
            localStorage.setItem(this.$store.state.court_number, JSON.stringify(wholeItem))
            // this.data.defendshow_is_counterclaim=this.$store.state.is_counterclaim
          }
        }
      },
      deep: true
    }
  }
}

</script>

